import http from '../httpConfig'

const table = 'mrbase_sys_users'

/**
 * @description: 列表
 * @param {type} 
 * @return: 
 * @author: lwq
 */
export const admin_account_list = (params = {}) => {
  params['~table~'] = table
  return new Promise((resolve, reject) => {
    http.post('/api/management/list-user/v1', params)
      .then(res => {
        resolve(res)
      })
      .catch(err => {
        reject(err)
      })
  })
}
